/* eslint-disable no-unused-vars */
import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";
// core version + navigation, pagination modules:
import Swiper, { Navigation, Pagination } from "swiper";
// import Swiper and modules styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

function refreshVerifiedStep() {
  htmx.ajax('GET', `/users/get-process/`, {
    target: "#isVerified",
    swap: "outerHTML",
  });
}

let message = "";
let title = "";



export default function Default() {
  return {
    openCreateUser: false,
    openCreateLocal: false,
    openCreateCareer: false,
    openCreateCar: false,
    openUpdateCreateCar: false,
    openCreateNews: false,

    loading:false,
    applying: false,
    readDetail: false,
    swiper: null,
    addDep: false,
    updating: false,
    searchWord: "",
    notif: false,
    pushLoading: false,
    userState: window.state,
    tmDetails: false,
    proDetails: false,
    tDetails: false,
    buyTicket: false,
    isVerified: false,
    amount: window.amount,
    showTicketModal: false,
    subscribed: 0,
    fullscreen: false,
    addEmail: false,
    driverText: "",
    edit: false,
    editKin: false,
    profile_img:
      "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png",
    post_img:
      "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png",
    editCard: false,
    switch_to_driver: null,
    reveal: false,
    pstep: 1,
    uMenu: false,
    notifications: false,
    hasUnreadNotifications: false,
    apps: false,
    lang: false,
    search: false,
    menu: false,
    showNav: false,
    navTop: false,
    lastScroll: 0,
    formData: {
      switch_to_driver: "",
    },



    openUserDetail(username) {
      this.proDetails = true;
      let link = `/users/get-detail/${username}/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
    },

    async updateCarType() {
      this.creating = true;
      const formElement = this.$refs.carUpdateForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      // data.append("title", document.getElementById("id_title").value);
      // data.append("amount", document.getElementById("id_amount").value);
      // data.append("seats", document.getElementById("id_seats").value);

      await sleep(3000);
      await axios
        .put(`${action}`, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.dir(response);
          await iziToast.success({
            title: "UPDATED CAR TYPE",
            message: "You have successfully updated a car type",
          });
          htmx.ajax("GET", `${location.origin}/settings/cars/table/`, {
            target: "#table",
            swap: "innerHTML",
          });

          htmx.ajax("GET", `${location.origin}/settings/cars/`, {
            target: "#slider",
            swap: "innerHTML",
          });
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "UPDATING CAR TYPE FAILED",
            message: e.message,
          });
          return;
        });

        this.openUpdateCreateCar = false;
    },


    async sendPushNotification() {
      this.pushLoading = true;
      const formElement = this.$refs.pushForm;
      const action = formElement.action;
      const token = formElement.dataset.csrf;
      let data = new FormData(formElement);

      await formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        data.append(fieldElement.name, fieldElement.value);
      });

      await sleep(3000);

      await axios
        .post(action, data, {
          headers: {
            CSRFToken: token,
          },
        })
        .then(async (response) => {
          await iziToast.success({
            title: "PUSH NOTIFICATION SENT",
            balloon: true,
            position: "topRight",
            animateInside: true,
            message: response.data.message,
          });
          this.pushLoading = false;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "PUSH NOTIFICATION SENT",
            balloon: true,
            position: "topRight",
            animateInside: true,
            message:
              "Something went wrong while sending the push notification. Please try again soon.",
          });
          this.pushLoading = false;
        });
      return;
    },

    show_item(el) {
      return this.searchWord === "" || el.textContent.includes(this.search);
    },

    fullscreenMode() {
      if (!document.fullscreenElement) {
        this.fullscreen = true;
        document.getElementById("body").requestFullscreen();
      } else if (
        document.fullscreenElement ||
        document.mozFullScreenElement ||
        document.msFullScreenElement ||
        document.webkitFullScreenElement
      ) {
        this.fullscreen = false;
        window.document.exitFullscreen();
      }
    },

    async dp(file) {
      this.profile_img = file;
    },

    alertCompleteVerification() {
      iziToast.info({
        title: "VERIFICATION INCOMPLETE",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "Complete your verification to be able to start taking rides.",
      });
    },

    async initState(state, ticket, isVerified) {
      console.log(state);
      if (state === 1) {
        this.switch_to_driver = true;
        this.driverText = "Driver Mode";
      } else if (state === 2) {
        this.switch_to_driver = false;
        this.driverText = "Rider Mode";
      }

      if (ticket === "True") {
        this.buyTicket = false;
      } else if (ticket === "False"){
        this.buyTicket = true;
      } else {
        this.buyTicket = null;
      }

      this.isVerified = isVerified;

      // if (window.history.length > 0) {
      //   console.log("History", window.history.length);
      //   console.log("History Popstate", window.history);
      //   this.back = window.history.length;
      // }

      if (document.getElementById("header")) {
        let main = document.getElementById("main");
        let navbar = document.getElementById("header");
        let header = document.getElementById("navbar");
        if (document.getElementById("userMenu") && header) {
          let userMenu = document.getElementById("userMenu");
          let nav_header_h = header.offsetHeight;
          userMenu.style.top = `${nav_header_h}px`;
        }

        // if (document.getElementById('sidebar')) {
        //     let sidebar = document.getElementById('sidebar');
        //     sidebar.classList.add(`-bottom-[${header_h}px])`);
        // }
        main.style.minHeight = "100vh"; //`calc(100vh - ${header_h}px)`;
        // nbar.style.height = `calc(100vh - ${header_h}px)`;
      }

      // const csrf_token = getCookie('csrftoken');
      await axios
        .get("/inbox/notifications/api/unread_list/?max=20/")
        .then((response) => {
          this.notifications = response.data.unread_list;
          this.hasUnreadNotifications = response.data.unread_count > 0;
        })
        .catch((err) => console.log(err));

      this.scrollNav;
    },

    async profileChosen(event) {
      this.fileToDataUrl(event, (src) => (this.profile_img = src));

      const formElement = this.$refs.dpForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      await formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        data.append(fieldElement.name, fieldElement.value);
      });

      console.dir(data);

      await axios
        .patch(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async function (response) {
          console.log(response);
          refreshVerifiedStep();
          if (response.status === 200 || response.status === 201) {
            await iziToast.success({
              title: "PHOTO UPDATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: response.data.message,
            });
          }
          await sleep(3000);
          return;
        })
        .catch(async function (error) {
          console.log(error);
          if (error.message) {
            await iziToast.error({
              title: "PROFILE UPDATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.message,
            });
            await sleep(3000);
            return;
          } else if (error.response.data.message) {
            await iziToast.error({
              title: "PROFILE UPDATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.message,
            });
            await sleep(3000);
            return;
          } else if (error.response.data.non_field_errors) {
            await iziToast.error({
              title: "PROFILE UPDATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.non_field_errors,
            });
            await sleep(3000);
            return;
          }
          await sleep(3000);
          return; // window.location.reload();
        });
      return;
    },

    async postChosen(event) {
      this.fileToDataUrl(event, (src) => (this.post_img = src));
    },

    fileToDataUrl(event, callback) {
      if (!event.target.files.length) return;

      let file = event.target.files[0],
        reader = new FileReader();

      reader.readAsDataURL(file);
      reader.onload = (e) => callback(e.target.result);
    },

    async markRead(slug) {
      await axios
        .get(`/${slug}/`)
        .then((res) => {
          if (res.status === 200) {
            iziToast.info({
              title: "NOTIFICATION READ",
              message: res.data.message,
            });
            this.hasUnreadNotifications = false;
          }
        })
        .catch((e) => {
          iziToast.error({
            title: "NOTIFICATION ERROR",
            message: e,
          });
        });
    },

    async switchForm() {
      this.switch_to_driver = !this.switch_to_driver;
      if (this.switch_to_driver === true) {
        this.driverText = "Driver";
      } else {
        this.driverText = "Rider";
      }

      const formElement = this.$refs.switchForm;
      const action = formElement.action;
      const data = new FormData(formElement);
      const token = formElement.dataset.token;

      if (this.switch_to_driver === true) {
        data.append('toggle', true);
      } else {
        data.append('toggle', false);
      }

      console.dir(data);

      if (this.switch_to_driver === true) {
        title = "DRIVER MODE";
        message = "You have switched to use the app as a driver";
      } else {
        title = "RIDER MODE";
        message = "You have switched to use the app as a rider";
      }

      await axios
        .post(action, data, {
            headers: {
              'Authorization': `Token ${token}`,
            },
          })
        .then(async function (response) {
          console.log(response);
          if (response.status === 200 || response.status === 201) {
            await iziToast.success({
              title: title,
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: message,
            });
          }
          await sleep(5000);
          return; // window.location.reload();
        })
        .catch(async function (error) {
          console.log(error);
          await iziToast.error({
            title: title,
            balloon: true,
            position: "topRight",
            animateInside: true,
            message: error.message,
          });
          await sleep(3500);
          return; // window.location.reload();
        });
      return;
    },

    scrollNav() {
      let currentScroll = window.pageYOffset;

      if (currentScroll <= 0) {
        this.showNav = false;

        this.lastScroll = currentScroll;
      }

      if (currentScroll > this.lastScroll) {
        this.showNav = true;
        this.navTop = true;

        this.lastScroll = currentScroll;
      } else if (currentScroll < this.lastScroll) {
        this.showNav = false;
        this.navTop = false;

        this.lastScroll = currentScroll;
      }

      // this.notif = false;
      // this.apps = false;
      this.lang = false;
      this.search = false;
      // this.menu = false;
    },

    openNotifBar() {
      this.notif = !this.notif;

      const main = document.getElementById("notifBar");

      if (this.notif) {
        main.classList.remove("w-0");
        main.classList.add(
          "w-56",
          "duration-500",
          "p-4",
          "transition:ease-in-out"
        );
      } else {
        main.classList.remove("w-56", "p-4");
        main.classList.add("w-0", "duration-500", "transition:ease-in-out");
      }
    },

    openAppsMenu() {
      this.apps = !this.apps;
    },

    openUserMenu() {
      this.uMenu = !this.uMenu;
    },

    showSearchBar() {
      this.search = !this.search;
    },

    showLanguages() {
      this.lang = !this.lang;
    },

    openMenuBar() {
      this.menu = !this.menu;
    },

    postUpdateText() {
      return this.loading ? "Processing..." : "Update";
    },

    postCreateText() {
      return this.loading ? "Processing..." : "Create Post";
    },

    formText() {
      return this.loading ? "Processing..." : "Login";
    },
    pushText() {
      return this.loading ? "Processing..." : "Send";
    },

  };
}
