import htmx from "htmx.org/dist/htmx";


export default function Transactions() {
  return {
    tDetails: false,
    
    openTransaction(slug) {
      let link = `/users/transactions/${slug}/detail/`;
      // let width = (window.innerWidth > 0) ? window.innerWidth : screen.width;
      // console.log('width: ', width);
      // if (width > 640) {
      htmx.ajax("GET", link, {
        target: "#tDetail",
        swap: "innerHTML",
      });
      this.tDetails = true;
      // }
    },
  };
}
