import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function SignupForm() {
  return {
    loading: false,
    togglePassword: false,
    formData: {
      old_password: "",
      new_password1: "",
      new_password2: "",
    },

    async validateField(formElement, fieldElement, el) {
      let data = new FormData(formElement);
      data.append("__field_name__", fieldElement.name);

      await axios
        .post(formElement.action, data)
        .then(async function (response) {
          let errors = response.data.errors;
          let errorsWrapperElement = document.getElementById(
            `error-wrapper-${fieldElement.name}`
          );

          if (errors.length === 0) {
            el = false;
            console.log(el);
            errorsWrapperElement.classList.add("hidden");
            if (errorsWrapperElement) {
              errorsWrapperElement.innerHTML = "";
            }
          } else {
            el = true;
            console.log(el);
            errorsWrapperElement.classList.remove("hidden");
            errorsWrapperElement.classList.add("flex");
            if (errorsWrapperElement) {
              let errorsHtml = "";
              for (let i = 0; i < errors.length; i++) {
                errorsHtml += `${errors[i]}`;
              }
              errorsWrapperElement.innerHTML = errorsHtml;
            }
          }
        });
      this.formError = el;
      console.log(el);
    },

    async submitForm() {

            this.loading = true;

            const formElement = this.$refs.form;
            const action = formElement.action;
            const csrf = formElement.dataset.csrf;
            const redirect = formElement.dataset.redirect;
            let data = new FormData(formElement);

            formElement.querySelectorAll("[name]").forEach((fieldElement) => {

                data.append(fieldElement.name, fieldElement.value);

            });

            await axios
              .post(action, data, {
                headers: {
                  "X-CSRFToken": csrf,
                },
              })
              .then(async function (response) {
                if (response.status === 200 || response.status === 201) {
                  await iziToast.success({
                    title: "PASSWORD CHANGED SUCCESSFULLY",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: `Successfully Changed your password.`,
                  });
                }
                await sleep(5000);
                if (redirect) {
                  return window.location.replace(
                    `${window.location.origin}${redirect}`
                  );
                }
              })
              .catch(async function (error) {
                console.log(error);
                if(error.response.data.old_password) {
                    await iziToast.error({
                        title: "PASSWORD CHANGE FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.old_password,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                } else if(error.response.data.new_password1) {
                    await iziToast.error({
                        title: "PASSWORD CHANGE FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.new_password1,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                } else if(error.response.data.new_password2) {
                    await iziToast.error({
                        title: "PASSWORD CHANGE FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.new_password2,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                } else {
                    await iziToast.error({
                        title: "PASSWORD CHANGE FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.non_field_errors,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                }
              });

            this.loading = false;
            return;
    },

    formText() {
      return this.loading ? "Processing..." : "Change Password";
    },
  };
}
