import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function Blog() {
  return {
    creating: false,
    updating: false,
    blogDetails: false,
    readDetail: false,

    post_img:
      "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png",


    openBlogDetailNow(slug) {
      let link = `/news/pages/${slug}/`;
      htmx.ajax("GET", link, {
        target: "#pagesDetail",
        swap: "innerHTML",
      });
      this.readDetail = !this.readDetail;
    },

    openPostDetail(slug) {
      this.blogDetails = true;
      let link = `/news/admin/${slug}/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
    },

    openPostUpdate(slug) {
      // this.post_img = image_url;
      this.blogDetails = true;
      let link = `/news/admin/${slug}/update/`;
      htmx.ajax("GET", link, {
        target: "#update",
        swap: "outerHTML",
      });
    },

    async updateBlog() {
      this.updating = true;
      const formElement = this.$refs.blogForm;
      const action = formElement.action;
      const slug = formElement.dataset.slug;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.name === "tags") {
          let values = fieldElement.value.split(",");

          data.append(fieldElement.name, values);
          console.log(values);
        } else if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        } else {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .put(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.log(response);
          await iziToast.success({
            title: "UPDATED POST",
            message: "You have successfully updated a post content",
          });
          await htmx.ajax("GET", `${location.origin}/news/admin/`, {
            target: "#blogList",
            swap: "innerHTML",
          });
          await htmx.ajax("GET", `${location.origin}/news/admin/${slug}/`, {
            target: "#uDetail",
            swap: "innerHTML",
          });
          this.updating = false;
          this.blogDetails = true;
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "UPDATED FAILED",
            message: e.message,
          });
          this.updating = false;
          return;
        });
    },

    async createPost() {
      this.updating = true;
      const formElement = this.$refs.postForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.name === "tags") {
          // let values = fieldElement.value.split(",");

          data.append(fieldElement.name, fieldElement.value);
          console.log(fieldElement.values);
        } else if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        } else {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .post(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.log(response);
          await iziToast.success({
            title: "CREATED POST",
            message: "You have successfully updated a post content",
          });
          htmx.ajax("GET", `${location.origin}/news/admin/`, {
            target: "#blogList",
            swap: "innerHTML",
          });
          htmx.ajax("GET", `${location.origin}/news/admin/${response.data.slug}/`, {
            target: "#uDetail",
            swap: "innerHTML",
          });
          this.updating = false;
          this.blogDetails = true;
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "CREATING POST FAILED",
            message: e.message,
          });
          this.updating = false;
          return;
        });
    },
  };
}
