let message = "";
let cardType = "";

async function verifyPayments(callback, token) {
  await axios
    .get(callback, {
      headers: {
        Authorization: `Token ${token}`,
      },
    })
    .then(function (response) {
      // Handle success
      console.log(response);
      iziToast.success({
        title: "PAYMENT VERIFICATION",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "You have successfully verified your card",
      });
    })
    .catch(function (error) {
      // Handle error
      console.log(error);
      iziToast.error({
        title: "PAYMENT VERIFICATION ERROR",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "The verification process failed",
      });
    });
}

// eslint-disable-next-line no-unused-vars
function getCardType(cardNumber) {
  // Define regular expressions for each card type
  var amexPattern = /^3[47][0-9]{13}$/;
  var dinersPattern = /^3(?:0[0-5]|[68][0-9])[0-9]{11}$/;
  var visaPattern = /^4[0-9]{12}(?:[0-9]{3})?$/;
  var mastercardPattern = /^5[1-5][0-9]{14}$/;
  var vervePattern = /^5061/;
  var discoverPattern = /^6(?:011|5[0-9]{2})[0-9]{12}$/;
  var auraPattern = /^(50[0-9]{4}|60699[0-9]{10})$/;

  // Test the card number against each pattern
  if (amexPattern.test(cardNumber)) {
    return "American Express";
  } else if (dinersPattern.test(cardNumber)) {
    return "Diners Club";
  } else if (visaPattern.test(cardNumber)) {
    return "Visa";
  } else if (mastercardPattern.test(cardNumber)) {
    return "Mastercard";
  } else if (vervePattern.test(cardNumber)) {
    return "Verve";
  } else if (discoverPattern.test(cardNumber)) {
    return "Discover";
  } else if (auraPattern.test(cardNumber)) {
    return "Aura";
  } else {
    return "Unknown";
  }
}

