    async closePopup() {
      // Get the query parameters from the popup window URL
      const queryParams = new URLSearchParams(window.location.search);

      // Retrieve the value of the 'param1' parameter
      const id = queryParams.get("p");
      const token = queryParams.get("t");
      const reference = queryParams.get("reference");

      // Log the value of the 'param1' parameter

      var callbackUrl = `/api/cards/${id}/verify/?trxref=${reference}&reference=${reference}`;
      console.log(`${id} - ${token} - ${reference} - ${callbackUrl}`);

      await verifyPayments(callbackUrl, token);
      window.close();
    },

