import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";

// core version + navigation, pagination modules:
import Swiper from "swiper";
// import Swiper and modules styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import Default from "../components/Default";

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function Cartype() {
  return {
    default: Default,
    creating: false,
    loading: false,

    // car type script completed and working
    carSlider() {
      this.swiper = new Swiper(this.$refs.container, {
        direction: "horizontal",
        loop: true,
        sidesPerView: 1,
      });
      return;
    },

    // editCarType(slug) {
    //   this.openUpdateCreateCar = true;

    //   htmx.ajax("GET", `${location.origin}/settings/cars/${slug}/update`, {
    //     target: "#updateCar",
    //     swap: "innerHTML",
    //   });
    // },

    async addCarType() {
      this.creating = true;
      const formElement = this.$refs.carForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .post(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.dir(response);
          await iziToast.success({
            title: "CREATED NEW CAR TYPE",
            message: "You have successfully new car type",
          });
          this.creating = false;
          this.openCreateCar = false;
          await htmx.ajax("GET", `${location.origin}/settings/cars/table/`, {
            target: "#table",
            swap: "outerHTML",
          });

          await htmx.ajax("GET", `${location.origin}/settings/cars/`, {
            target: "#slider",
            swap: "innerHTML",
          });
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "CREATING CAR TYPE FAILED",
            message: e.message,
          });
          this.creating = false;
          // this.openCreateLocal = false;
          return;
        });
    },
    // car type script ending
  };
}
