import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function Localization() {
  return {
    loading: false,

    // Localization is completed for admin
    openLocalizationDetail(uuid) {
      let link = `/settings/localization/${uuid}/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
      this.proDetails = true;
    },

    async addLocale() {
      this.loading = true;
      const formElement = this.$refs.localeForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .post(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          await iziToast.success({
            title: "CREATED NEW LOCALE",
            message: "You have successfully new operational location",
          });
          this.proDetails = true;
          this.loading = false;
          this.openCreateLocal = false;
          await htmx.ajax("GET", `${location.origin}/settings/localization/`, {
            target: "#userList",
            swap: "innerHTML",
          });

          await htmx.ajax(
            "GET",
            `${location.origin}/settings/localization/${response.data.uuid}/`,
            {
              target: "#uDetail",
              swap: "innerHTML",
            }
          );
          return;
        })
        .catch(async (e) => {
          if (e.response.data.state) {
            await iziToast.error({
              title: "CREATING LOCALE FAILED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: e.response.data.state,
            });
          } else {
            await iziToast.error({
              title: "CREATING LOCALE FAILED",
              message: e.message,
            });
          }

          this.loading = false;
          // this.openCreateLocal = false;
          return;
        });
    },

    async updateLocale() {
      this.updating = true;
      const formElement = this.$refs.localeForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .put(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          await iziToast.success({
            title: "UPDATED LOCALIZATION",
            message: "You have successfully updated a locale",
          });

          await htmx.ajax("GET", `${location.origin}/settings/localization/`, {
            target: "#userList",
            swap: "innerHTML",
          });

          await htmx.ajax(
            "GET",
            `${location.origin}/settings/localization/${response.data.uuid}/`,
            {
              target: "#uDetail",
              swap: "innerHTML",
            }
          );

          this.updating = false;
          this.proDetails = true;
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "UPDATING LOCALIZATION FAILED",
            message: e.message,
          });
          this.updating = false;
          return;
        });
    },

    openLocaleUpdate(uuid) {
      this.proDetails = true;
      let link = `${location.origin}/settings/localization/${uuid}/update/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
    },
    // end localization javascript admin
  };
}
