import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function Career() {
  return {
    creating: false,
    applying: false,
    careerDetails: false,
    updating: false,
    readDetail: false,

    applyText() {
      return this.applying ? "Submitting..." : "Apply Now";
    },

    // career is completed for the admin
    openApplicant(id) {
      let link = `/careers/applicant/${id}/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
      this.careerDetails = true;
    },

    openCareerDetailNow(slug) {
      let link = `/careers/pages/${slug}/`;
      htmx.ajax("GET", link, {
        target: "#pagesDetail",
        swap: "innerHTML",
      });
      this.readDetail = !this.readDetail;
    },

    async addDepForm() {
      const formElement = this.$refs.depForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        data.append(fieldElement.name, fieldElement.value);
      });

      console.log(data);

      await sleep(3000);
      await axios
        .post(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.log(response);
          await iziToast.success({
            title: "ADDED DEPARTMENT",
            message: "You have successfully added a new department",
          });
          this.updating = false;
          htmx.ajax("GET", `/careers/departments/`, {
            target: "#dep",
            swap: "innerHTML",
          });
          this.addDep = false;
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "ADDING DEPARTMENT FAILED",
            message: e.message,
          });
          this.updating = false;
          return;
        });
    },

    openCareerDetail(slug) {
      this.careerDetails = true;
      let link = `/careers/${slug}/`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
    },

    openCareerUpdate(slug) {
      this.careerDetails = true;
      let link = `/careers/${slug}/update`;
      htmx.ajax("GET", link, {
        target: "#uDetail",
        swap: "innerHTML",
      });
    },

    async openCreateCareerForm(slug) {
      console.log(slug);
      let link = `/careers/create/`;
      await htmx.ajax("GET", link, {
        target: "#userModal",
        swap: "innerHTML",
      });
      this.openCreateCareer = true;
    },

    async createCareer() {
      this.creating = true;
      const formElement = this.$refs.careerForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .post(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          await iziToast.success({
            title: "CREATTED CAREER",
            message: "You have successfully created a career listing",
          });
          htmx.ajax("GET", `${location.origin}/careers/`, {
            target: "#careerList",
            swap: "innerHTML",
          });

          this.careerDetails = true;
          htmx.ajax(
            "GET",
            `${location.origin}/careers/${response.data.slug}/`,
            {
              target: "#uDetail",
              swap: "innerHTML",
            }
          );

          this.creating = false;
          this.openCreateCareer = false;

        })
        .catch(async (e) => {
          if (e.response.data.summary) {
            await iziToast.error({
              title: "CAREER CREATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: `SUMARRY: ${e.response.data.summary}`,
            });
          } else if (e.response.data.non_field_errors) {
            await iziToast.error({
              title: "CAREER CREATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: e.response.data.non_field_errors,
            });
          } else if (e.message) {
            await iziToast.error({
              title: "CAREER CREATE",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: e.message,
            });
          }
          this.creating = false;
          return;
        });
    },

    async applyCareer() {
      this.applying = true;
      const formElement = this.$refs.careerForm;
      const action = formElement.action;
      const slug = formElement.dataset.slug;
      const csrf = formElement.dataset.csrf;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        // if (fieldElement.name === "position") {
        //   data.append(fieldElement.name, parseInt(fieldElement.value));
        // }

        if (
          fieldElement.type !== "textarea" &&
          fieldElement.name !== "position"
        ) {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await axios
        .post(action, data, {
          headers: {
            "X-CSRFToken": csrf,
          },
        })
        .then(async (response) => {
          console.log(response);
          await iziToast.success({
            title: "SUCCESSFULLY APPLIED",
            message: "You have successfully applied to a job listing",
          });
          this.applying = false;
          await sleep(3000);
          this.openCareerDetailNow(slug);

          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "APPLICATION FAILED",
            message: e.message,
          });
          this.applying = false;
          return;
        });
    },

    async updateCareer() {
      this.updating = true;
      const formElement = this.$refs.careerForm;
      const action = formElement.action;
      const slug = formElement.dataset.slug;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if (fieldElement.type === "textarea") {
          let textarea = fieldElement.id;
          console.log(
            "textarea content: ",
            window.parent.tinymce.get(textarea).getContent()
          );
          data.append(
            fieldElement.name,
            window.parent.tinymce.get(textarea).getContent()
          );
        }

        if (fieldElement.type !== "textarea") {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      console.log(data);

      await sleep(3000);
      await axios
        .put(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async (response) => {
          console.log(response);
          await iziToast.success({
            title: "UPDATED CAREER",
            message: "You have successfully updated a career listing",
          });

          await htmx.ajax("GET", `${location.origin}/careers/`, {
            target: "#careerList",
            swap: "innerHTML",
          });

          await htmx.ajax("GET", `${location.origin}/careers/${slug}/`, {
            target: "#uDetail",
            swap: "innerHTML",
          });
          this.updating = false;
          this.careerDetails = true;
          return;
        })
        .catch(async (e) => {
          await iziToast.error({
            title: "UPDATING CAREER FAILED",
            message: e.message,
          });
          this.updating = false;
          return;
        });
    },
    // end career javascript for admin
  };
}
