import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function SignupForm() {
  return {
    loading: false,
    togglePassword: false,
    step: 1,
    is_driver: null,
    formError: false,
    formData: {
      is_driver: "",
      first_name: "",
      last_name: "",
      middle_name: "",
      gender: "",

      username: "",
      email: "",
      password1: "",
      password2: "",
      gave_consent: false,
    },

    hasUppercase: false,
    hasDigit: false,
    hasSpecialChar: false,
    checkPasswordMatch: false,
    checkEmail:false,

    serverCheckPasswordMatch() {
      this.checkPasswordMatch = (this.formData.password1.length === this.formData.password2.length) && (this.formData.password1 === this.formData.password2);
    },

    serverCheckEmail() {
      this.checkEmail = this.formData.email.length > 1 && /[@]/.test(this.formData.email) && !this.formData.email.includes("admin@dropsride.com") && !this.formData.email.includes("dropsride.com");
    },

    next() {
      if (
        this.formData.is_driver !== null &&
        this.formData.first_name.length > 1 &&
        this.formData.last_name.length > 1 &&
        this.formData.middle_name.length > 1
      ) {
        return this.step++;
      }
      iziToast.warning({
        title: "FORM ERROR",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "Please fill all fields. They are required and cannot be left blank",
      });
    },

    back() {
      this.step--;
    },

    async submitForm() {
        // const userState = document.getElementById('id_userstate').value;

        if(this.checkServerValidity) {
            this.loading = true;

            const formElement = this.$refs.form;
            const action = formElement.action;
            const csrf = formElement.dataset.csrf;
            const redirect = formElement.dataset.redirect;
            let data = new FormData(formElement);

            formElement.querySelectorAll("[name]").forEach((fieldElement) => {
              if (fieldElement.name === "is_driver") {
                console.log("Axios signup form", this.formData.is_driver);
                if(this.formData.is_driver === "True" ) {
                  data.append("is_driver", true);
                } else {
                  data.append("is_driver", false);
                }
              // } else if (fieldElement.name === "userState") {
              //   data.append(fieldElement.name, userState);
              }
              else if (fieldElement.name !== "userState" && fieldElement.name !== "is_driver") {
                data.append(fieldElement.name, fieldElement.value);
              }

            });

            await axios
              .post(action, data, {
                headers: {
                  "X-CSRFToken": csrf,
                },
              })
              .then(async function (response) {
                console.log(response);
                if (response.status === 200 || response.status === 201) {
                  await iziToast.success({
                    title: "REGISTRATION COMPLETED",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: `You have successfully registered on our platform, please do well to fill in required profile information to get verified quickly.`,
                  });
                }
                await sleep(5000);
                if (redirect) {
                  return window.location.replace(
                    `${window.location.origin}${redirect}`
                  );
                }
              })
              .catch(async function (error) {
                console.log(error);
                if(error.response.data.email) {
                  await iziToast.error({
                    title: "REGISTRATION FAILED/ERROR",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: error.response.data.email,
                  });
                  await sleep(3500);
                  return;
                } else if (error.response.data.username) {
                  await iziToast.error({
                    title: "REGISTRATION FAILED/ERROR",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: error.response.data.email,
                  });
                  await sleep(3500);
                  return;
                } else if(error.response.data.non_field_errors) {
                  await iziToast.error({
                    title: "REGISTRATION FAILED/ERROR",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: error.response.data.non_field_errors,
                  });
                  await sleep(3500);
                  return;// window.location.reload();
                } else {
                  await iziToast.error({
                    title: "REGISTRATION FAILED/ERROR",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: error.message,
                  });
                  await sleep(3500);
                  return;// window.location.reload();
                }
              });

            this.loading = false;
            return;
        }
        await iziToast.info({
            title: "FORM ERROR",
            balloon: true,
            position: "topRight",
            animateInside: true,
            message: "Please fill all fields correctly. They are required and cannot be left blank",
        });
    },

    checkServerValidity() {
      var digit = /[0-9]/;
      this.hasDigit = digit.test(this.formData.password1);

      var specialChar = /[@/*$%.]/;
      this.hasSpecialChar = specialChar.test(this.formData.password1);

      var uppercase = /[A-Z]/;
      this.hasUppercase = uppercase.test(this.formData.password1);

      this.serverCheckPasswordMatch;

      this.serverCheckEmail;

      return this.hasDigit === this.hasSpecialChar === this.hasUppercase === this.checkPasswordMatch === this.checkEmail;
    },

    formText() {
      return this.loading ? "Processing..." : "Submit";
    },
  };
}
