// import moment from "moment";
import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now

// let isoDate = "2021-09-19T05:30:00.000Z";
// let newDate =  moment.utc(isoDate).format('MM/DD/YY');
function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function SubscriptionForm() {
  return {
    sub_loading: false,
    showTicketModal: false,
    valid: false,
    chargeForm: true,
    otpForm: false,
    pinForm: false,
    ticketData: {
      type: "card",
      authorization: "",
      amount: 0,
      ref: '',
    },

    async completeSubscription() {
      const formElement = this.$refs.tiketForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        if(fieldElement.name === 'pin') {
          data.append(fieldElement.name, fieldElement.value);
        } else if(fieldElement.name === 'otp'){
          data.append(fieldElement.name, fieldElement.value);
        } else {
          data.append(fieldElement.name, fieldElement.value);
        }
      });

      await sleep(3000);

      await axios.post(action, data, {
        headers: {
          "Authorization": `Token ${token}`,
        }
      }).then(async (res) => {

        await iziToast.success({
          title: 'TICKET ENABLLED',
          message: res.data.message
        });
        this.sub_loading = false;
        await sleep(3000);
        return window.location.reload();
      }).catch(async (e) => {
        await iziToast.error({
          title: 'PAYMENT FAILED',
          mssage: e.response.message
        });
        this.sub_loading = false;
        await sleep(3000);
        return window.location.reload();
      });
    },

    async chargeSubscription() {
      this.sub_loading = true;
        const formElement = this.$refs.tiketForm;
        const action = formElement.action;
        const token = formElement.dataset.token;
        let data = new FormData(formElement);

        formElement.querySelectorAll("[name]").forEach((fieldElement) => {
          if(fieldElement.type === 'radio') {
            data.append(fieldElement.name, this.ticketData.type);
          } else if(fieldElement.name === 'authorization_code') {
            data.append(fieldElement.name, this.ticketData.authorization);
          } else {
            data.append(fieldElement.name, fieldElement.value);
          }
        });

        await sleep(3000);

        await axios.post(action, data, {
          headers: {
            "Authorization": `Token ${token}`,
          }
        }).then(async (res) => {
          if(res.data.sub_id && !res.data.message.includes('otp') && !res.data.message.includes('pin')) {
              await iziToast.success({
                title: 'TICKET ENABLLED',
                message: res.data.message
              });
              this.sub_loading = false;
              await sleep(3000);
              return window.location.reload();
          } else if(res.data.message.includes('OTP')){
            this.chargeForm = false;
            this.otpForm = true;
            this.ticketData.ref = res.data.ref;
          } else if(res.data.message.includes('PIN')){
            this.chargeForm = false;
            this.pinForm = true;
            this.ticketData.ref = res.data.ref;
          }
          this.sub_loading = false;
          await sleep(3000);
          return;
        }).catch(async (e) => {
          await iziToast.error({
            title: 'PAYMENT FAILED',
            mssage: e.response.message
          });
          this.sub_loading = false;
          this.showTicketModal = false;
          await sleep(3000);
          return;
        });
    },

    formText() {
      return this.loading ? "Processing..." : "Pay Now";
    },
    pinText() {
      return this.loading ? "Processing..." : "Submit PIN";
    },
    otpText() {
      return this.loading ? "Processing..." : "Submit OTP";
    },
  };
}
