import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

export default function SignupForm() {
  return {
    loading: false,
    togglePassword: false,
    formError: false,
    formData: {
      login: "",
      remember: false,
      password: "",
    },

    checkEmail() {
        if(this.formData.login.length > 4 && /[@]/.test(this.formData.login) === true) {
            return true;
        }
        return false;
    },

    async submitForm() {
        if(
            this.formData.login.length > 1 && this.formData.password.length > 4

        ) {
            this.loading = true;

            const formElement = this.$refs.form;
            const action = formElement.action;
            const csrf = formElement.dataset.csrf;
            const redirect = formElement.dataset.redirect;
            let data = new FormData(formElement);

            formElement.querySelectorAll("[name]").forEach((fieldElement) => {
                if(fieldElement.name === "login") {
                  data.append('email', fieldElement.value);
                  data.append(fieldElement.name, fieldElement.value);
                }
                data.append(fieldElement.name, fieldElement.value);
                console.log(fieldElement.value);
            });

            console.log(data);

            await axios
              .post(action, data, {
                headers: {
                  "X-CSRFToken": csrf,
                },
              })
              .then(async function (response) {
                console.log(response);
                if (response.status === 200 || response.status === 201) {
                  await iziToast.success({
                    title: "AUTHENTICATED SUCCESSFULLY",
                    balloon: true,
                    position: "topRight",
                    animateInside: true,
                    message: `Authorization Token: <strong class="font-black">${response.data.key}</strong>`,
                  });
                }
                await sleep(5000);
                if (redirect) {
                  return window.location.replace(
                    `${window.location.origin}${redirect}`
                  );
                }
              })
              .catch(async function (error) {
                if(error.response.data.login) {
                    await iziToast.error({
                        title: "AUTHENTICATION FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.login,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                } else if(error.response.data.password) {
                    await iziToast.error({
                        title: "AUTHENTICATION FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.password,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                } else {
                    await iziToast.error({
                        title: "AUTHENTICATION FAILED/ERROR",
                        balloon: true,
                        position: "topRight",
                        animateInside: true,
                        message: error.response.data.non_field_errors,
                    });
                    await sleep(3500);
                    return;// window.location.reload();
                }
              });

            this.loading = false;
            return;
        }
        await iziToast.info({
            title: "FORM ERROR",
            balloon: true,
            position: "topRight",
            animateInside: true,
            message: "Please fill all fields. Passwords must be 4 characters long",
        });
    },

    checkServerValidity() {
      this.checkEmail;
    },

    formText() {
      return this.loading ? "Processing..." : "Login";
    },
  };
}
